<template>
  <div>
    <div class="py-4 px-2 shadow bg-blue-light text-white font-bold">
      <p class="mt-1 font-semibold text-xs float-right mr-3">
        Face Recognition
      </p>
      <div @click="$router.go(-1)" class="ml-2 cursor-pointer">
          <svg  width="20" height="20" style="fill: white;" class="d-inline mr-1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.--><path d="M9.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L77.3 256 246.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z"/></svg>
      </div>
    </div>
    <div class="row">
      <div
        class="col-sm-12"
        v-if="!streamOn"
        style="height: 100vh"
        @click="
          getStream('video');
          streamOn = true;
          $forceUpdate();
        "
      >
        <div
          style="height: 90vh"
          class="d-flex justify-content-center align-items-center shadow-lg rounded-lg"
        >
          <div style="width: 100%">
            <p class="text-center text-xl font-semibold">
              Klik disini untuk Start Camera!
            </p>
          </div>
        </div>
      </div>
      <div v-else class="col-sm-12 text-center">
         <video
            id="videoQRCODE"
            style=""
            autoplay
            muted
            @click="startScan"
          ></video>
        <!-- <video
          autoplay
          playsInline
          mutedstyle="height:60vh; width: 100vh;"
          id="videoQRCODE"
        ></video> -->
        <div class="text-center rounded-t-lg shadow background_gps p-3">
          <div style="height: 70px">
          <div class="row justify-content-center">
            <div class="col-9">
              <div class="text-center">
                <button
                  @click="startScan"
                  type="submit"
                  class="p-3 mt-2 btn btn-sm btn-warning rounded-lg font-semibold btn-block text-sm"
                >
                  <svg width="17" height="17" style="fill: black;" class="d-inline mr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><!--!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.--><path d="M220.6 121.2L271.1 96 448 96v96H333.2c-21.9-15.1-48.5-24-77.2-24s-55.2 8.9-77.2 24H64V128H192c9.9 0 19.7-2.3 28.6-6.8zM0 128V416c0 35.3 28.7 64 64 64H448c35.3 0 64-28.7 64-64V96c0-35.3-28.7-64-64-64H271.1c-9.9 0-19.7 2.3-28.6 6.8L192 64H160V48c0-8.8-7.2-16-16-16H80c-8.8 0-16 7.2-16 16l0 16C28.7 64 0 92.7 0 128zM168 304a88 88 0 1 1 176 0 88 88 0 1 1 -176 0z"/></svg>
                   Take Picture
                </button>
              </div>
            </div>
          </div>
          <p class="text-xs text-white font-semibold mt-2">Ekspresi terdeteksi: {{expressions}}</p>
            <p class="text-xs p-3 font-semibold text-white" v-if="message==''">
              Remove Mask and any other accessories before take a selfie !
            </p>
          <div class="text-xs p-3 font-semibold text-white">
            <p v-if="message!=''">{{message}}</p>
          </div>
          </div>
          <br>
          <br>
          <br>
          <br>
          <br>
        </div>
        <canvas id="canvas" style="overflow: auto" class="d-none"></canvas>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import autophp from "@/plugins/auto";
let sdb = new autophp("ci");
import * as faceapi from "@vladmandic/face-api";
// import * as H from "@/static/dist/human.esm.js"; // equivalent of import @vladmandic/Human

export default {
  data() {
    return {
      isIos: false,
      countIos: 0,
      streamOn: false,
      expressions:null,
      listUsers:[],
      urutanEkspresi:[],
      message:'',
      listExpressions:[]
    };
  },
  methods: {
    async startScan(videoEl) {
      try{

        
        this.message=''
        let video = document.getElementById("videoQRCODE");
        let that = this;
        // menginsert element setelah element yg menjadi parameter
        // membuat element canvas dari video element
        var canvas = await faceapi.createCanvasFromMedia(video);
        canvas.style.position = "absolute;";
        var displaySize = { width: canvas.width, height: canvas.height };
        console.log(displaySize)
        // sdb.alert('scanning ...','bg-green-400')
        faceapi.euclideanDistance([0, 0], [0, 20]);
        // menginitialize dimensi canvas untuk wajah
        faceapi.matchDimensions(canvas, displaySize);
        // inputSize disini bisa berpariasi, semakin kecil semakin cepat refresh scannya akan tetapi less akurat, semakin besar semakin akurat
        // contoh : untuk webcam. 128, 160, for detecting smaller faces use larger sizes, e.g. 512, 608 | default: 416
        // keselurahan fungsi dibawah adalah untuk mengambil descriptor dari wajah yang akan model wajah
        var fullFaceDescriptions = null;
        // fullFaceDescription = await faceapi.detectSingleFace(video).withFaceLandmarks().withFaceDescriptor();
        fullFaceDescriptions = await faceapi.detectAllFaces(video).withFaceLandmarks().withFaceDescriptors().withFaceExpressions();
        console.log('fullFaceDescription',fullFaceDescriptions)
        // fullFaceDescriptions = await faceapi.detectAllFaces(video,new faceapi.TinyFaceDetectorOptions({inputSize: parseInt(that.$store.state.scanSize),})).withFaceLandmarks(true).withFaceDescriptors();
        // console.log('change',fullFaceDescriptions[0].landmarks.getRefPointsForAlignment())
        console.log("change", fullFaceDescriptions[0].landmarks.getMouth());
        let mulut = fullFaceDescriptions[0].landmarks.getMouth();
        console.log("mulut", mulut);
        console.log(fullFaceDescriptions)

        // console.log('change',fullFaceDescriptions[0].landmarks.getRefPointsForAlignment)
        // console.log('getJawOutline',fullFaceDescriptions[0].landmarks)
        // ============================= DRAW ON videoEl, bagian ini berfungsi untuk mendraw box dan landmark diwajah
        const resizedDetections = faceapi.resizeResults(
          fullFaceDescriptions,
          displaySize
        );
        console.log( canvas.width, canvas.height)
        canvas.getContext("2d").clearRect(0, 0, canvas.width, canvas.height);
        faceapi.draw.drawDetections(canvas, resizedDetections);
        faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);
        // faceapi.draw.drawFaceExpressions(canvas, resizedDetections)
        // ========================================
        console.log(that.$store.state.employee)
        let docs=[{
          facejson:JSON.parse(that.$store.state.employee.faces)
        }]
          const labeledFaceDescriptors = await Promise.all(
            docs.map(async (label) => {
              console.log('label',label)
              // ============================= fetch image data from urls and convert blob to HTMLImage element
              // const imgUrl = `https://akbarmaliki.github.io/wajah.jpg`
              // const img = await faceapi.fetchImage(imgUrl)
              // ============================= menggunakan data gambar dari blob yg telah disimpan
              // var images = document.createElement('img');
              // images.src=label.facedata;
              // const img = images;
              // // detect the face with the highest score in the image and compute it's landmarks and face descriptor
              // const fullFaceDescription = await faceapi.detectSingleFace(img,new faceapi.TinyFaceDetectorOptions({ inputSize: 160 })).withFaceLandmarks(useTinyModel).withFaceDescriptor()
              // // jika undefined maka throw error no faces detected
              // if (!fullFaceDescription) {
              //     throw new Error(`no faces detected for ${label}`)
              // }
              // ==============================
              // mengconvert data face descriptor ke type Float32Array
              if (label.facejson) {
                const faceDescriptors = [
                  // new Float32Array(fullFaceDescription.descriptor) // dari facedata / gambar 1 data
                  // new Float32Array(Object.values(label.facejson[0]._descriptors[0])) // dari facejson 1 data
                ];
                label.facejson.forEach((e) => {
                  faceDescriptors.push(new Float32Array(Object.values(e[0]))); // multiple facejson data
                });
                // console.log('faceDescriptors',faceDescriptors)
                // PILIH FIELD MANA YANG AKAN DI JADIKAN SEBAGAI DESCRIPTOR TEXT
                return new faceapi.LabeledFaceDescriptors(
                  JSON.stringify({
                    EMP_NO: that.$store.state.employee_id,
                    FULL_NAME: that.$store.state.employee.name,
                    _id: that.$store.state.employee_id,
                  }),
                  faceDescriptors
                );
              }
            })
          );
          // max confidence antara wajah descriptor
          const maxDescriptorDistance =
            parseInt(that.$store.state.akurasiWajah) / 100;
          // mendapatkan wajah yang memiliki confidance descriptor yang paling mendekati
          const faceMatcher = new faceapi.FaceMatcher(
            labeledFaceDescriptors,
            maxDescriptorDistance
          );

          // mengambil hanya data yang descriptor nya paling best match
          const results = fullFaceDescriptions.map((fd) =>
            faceMatcher.findBestMatch(fd.descriptor)
          );
          if (results.length > 0) {
            if (results[0]._label == "unknown") {
              sdb.alert(
                "Wajah bukan milik "+that.$store.state.employee.name,
                "bg-black"
              );
              return false
            }
            that.getExpression(fullFaceDescriptions[0].expressions)

            // menginsert boxcanvas setelah wajah terdeteksi
            // that.insertAfter(videoEl, canvas);
            document.querySelectorAll("canvas").forEach((e, i) => {
              if (i > 0) {
                e.remove();
              }
            });
            results.forEach(async (bestMatch, i) => {
              // const box = fullFaceDescriptions[i].detection.box
              // menaruh data wajah yang paling best match ke variable faceid
              console.log(bestMatch)
              const hasil = JSON.parse(bestMatch.label);
              const distance = JSON.parse(bestMatch._distance)
              console.log(distance)
              console.log(hasil)
              that.faceid = hasil;
              // that.cekTypeAlokasi(that.faceid);
              if ((100 - (distance * 100))>60){
                  sdb.alert("Wajah ditemukan!", "bg-green-400");
                  that.loading = false;
                  that.cekExpressionAbsen(hasil,canvas);
                  // if (
                  //   !confirm(
                  //     `Apakah wajah ini benar milik ${hasil["FULL_NAME"]} ?`
                  //   )
                  // ) {
                  //   return;
                  // }
                  // that.lanjutAbsen(hasil);
              } else{
                sdb.alert(`Persentase hanya ${100-distance*100}`, "bg-green-400");
              }
              // const drawBox = new faceapi.draw.DrawBox(box, { label: hasil.EMP_NO+" - "+hasil.nama })
              // drawBox.draw(canvas)

              // menghapus semua canvas dari index ke 1 dan seterusnya, untuk menghilangkan duplicate box
            });
            } else {
              that.loading = false;
              sdb.alert("Data tidak ada atau tidak ditemukan di gang ini!");
            } 
          }catch(e){
            console.log("error")
            sdb.alert("Recognition wajah gagal")
          }
    },
     cekExpressionAbsen(user,canvas){
        let that=this;
        if(this.listExpressions.length==0){
            // if(this.expressions=='happy' || this.expressions=='sad' || this.expressions=='angry' || this.expressions=='fearful' || this.expressions=='disgusted' || this.expressions=='surprised'){
            //     sdb.alert('Ekspresi Pertama harus Netral!');
            //     return;
            // }
            this.listExpressions[0]=this.expressions;
        }
        if(this.listUsers.length==0){
            this.listUsers[0]=user.EMP_NO;
        }else{
            this.listUsers[1]=user.EMP_NO;
        }
        console.log('checkd isini==============')
        console.log(this.listUsers)
        console.log(this.expressions)
        if(this.listUsers.length>0 && this.listExpressions.length>0){
            if(this.listUsers[0]!=user.EMP_NO){
                this.listUsers[0]=null;
                sdb.alert('Data Perbandingan user tidak sama!')
                return;
            }
            if(this.listExpressions[0]!=this.expressions){
              // sdb.alert('WOKE')
                // this.urutanEkspresi.push(user.EMP_NO);
                // if(this.urutanEkspresi.length>2){
                //     if(this.urutanEkspresi[this.urutanEkspresi.length-0]==this.urutanEkspresi[this.urutanEkspresi.length-1]){
                        // this.urutanEkspresi=[];
                //     }else{
                //         this.message=`Wajah Milik ${user['FULL_NAME']}, \n Gunakan Ekspresi yang berbeda!!`;
                //         return;
                //     }
                // }else{
                //     this.message=`Wajah Milik ${user['FULL_NAME']}, \n Gunakan Ekspresi yang berbeda!!`;
                //     return;
                // }
                that.prosesAbsen(canvas)
            }else{
                    sdb.alert(`Validasi ekspresi ${user['FULL_NAME']}, \n Gunakan Ekspresi yang berbeda!!`);
                    that.message=`Validasi ekspresi ${user['FULL_NAME']}, \n Ubah Ekspresi anda seperti senyum/buka mulut/dll`
            }
        }
        this.$forceUpdate();
    },
    async prosesAbsen(canvas) {
      window.checktimes.remove({},{multi:true},(err,docs)=>{});
      let that = this;
      sdb.loadingOn();
      let video = document.getElementById("videoQRCODE");

      // Create a canvas element to draw the video frame
      var canvas = document.createElement('canvas');
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      var ctx = canvas.getContext('2d');
      ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
      var dataURL = canvas.toDataURL('image/png');
     
        this.$store.state.checktimes.unshift({
          'id_user':this.$store.state.hit_id,
          'name':this.$store.state.employee.name,
          'checktime':that.formatTgl(new Date(),'YYYY-MM-DD HH:mm:ss'),
          'dataURL':dataURL,
          'work_location': that.$store.state.employee.work_location_id,
          'lat':that.$store.state.lat,
          'lng':that.$store.state.lng,
          'type_location':that.$store.state.location,
          'source_data':'local'
        })
      window.checktimes.insert(this.$store.state.checktimes,(err,docs)=>{});
      sdb.alert("Absen Berhasil",'bg-blue-400')
      sdb.loadingOff();
      this.$router.push('/absen/checktime')
    },
    getExpression(expressions){
            let that=this;
            expressions.neutral=Math.round(Math.abs(parseFloat(expressions.neutral)-1))
            expressions.happy=Math.round(Math.abs(parseFloat(expressions.happy)-1))
            expressions.sad=Math.round(Math.abs(parseFloat(expressions.sad)-1))
            expressions.angry=Math.round(Math.abs(parseFloat(expressions.angry)-1))
            expressions.fearful=Math.round(Math.abs(parseFloat(expressions.fearful)-1))
            expressions.disgusted=Math.round(Math.abs(parseFloat(expressions.disgusted)-1))
            expressions.surprised=Math.round(Math.abs(parseFloat(expressions.surprised)-1))
            if(expressions.neutral==0){
                that.expressions='neutral'
            }else if(expressions.happy==0){
                that.expressions='happy'
            }else if(expressions.sad==0){
                that.expressions='sad'
            }else if(expressions.angry==0){
                that.expressions='angry'
            }else if(expressions.fearful==0){
                that.expressions='fearful'
            }else if(expressions.disgusted==0){
                that.expressions='disgusted'
            }else if(expressions.surprised==0){
                that.expressions='surprised'
            }
        },
    getImage() {
      let that = this;
        let video = document.getElementById("videoQRCODE");
        var canvas = document.getElementById("canvas");
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        canvas
          .getContext("2d")
          .drawImage(video, 0, 0, video.videoWidth, video.videoHeight); // for drawing the video element on the canvas
        canvas.toBlob((blob) => {
          let file = new File([blob], "photo.jpg", { type: "image/jpeg" });
          that.prosesAbsen(file);
        }, "image/jpeg");
    },
    getStream(type) {
      let that = this;
      setTimeout(() => {
        const video = document.getElementById("videoQRCODE");
        console.log('video',video)
        navigator.mediaDevices
          .getUserMedia({
            video: {
              facingMode: that.$store.state.camera?'user':'environment',
              // facingMode: "user",
              resizeMode: "true",
              // , width: { ideal: 512 },height:{ideal:512}
            },
          })
          .then(function (mediaStream) {
            that.loading = false;
            that.$forceUpdate();
            video.srcObject = mediaStream;
            video.src = window.URL.createObjectURL(mediaStream);
            video.play();
          });
      }, 2000);
    },
    getUserMedia(constraints) {
      // if Promise-based API is available, use it
      if (navigator.mediaDevices) {
        return navigator.mediaDevices.getUserMedia({
          video: {
            facingMode: "user", //'environment':'user',
          },
        });
      }

      // otherwise try falling back to old, possibly prefixed API...
      var legacyApi =
        navigator.getUserMedia ||
        navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia ||
        navigator.msGetUserMedia;

      if (legacyApi) {
        // ...and promisify it
        return new Promise(function (resolve, reject) {
          legacyApi.bind(navigator)(constraints, resolve, reject);
        });
      }
    },
    formatTgl(tgl, pattern = "YYYY-MM-DD") {
      return this.$datefns.format(new Date(tgl), pattern);
    },
  },
  async mounted() {
    let that = this;
    // if (localStorage.getItem("users")) {
    // } else {
    //   this.$router.push("/");
    // }
  },
};
</script>


<style scoped>
.background_gps {
  background: rgb(0, 212, 255);
  background: linear-gradient(
    90deg,
    rgba(0, 212, 255, 1) 0%,
    rgba(9, 9, 121, 1) 100%,
    rgba(2, 0, 36, 1) 100%
  );
  border-radius: 0 0 20px 20px;
}
</style>